@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';


.wrapper {
    &.contents_store {
        .container_wrapper {
            .contents_wrap {
                padding-top: 0;
            }
        }
    
        //매장안내 상단고정바 드롭다운 메뉴
        .store_dropdown_wrap {
            position: relative;
            display: inline-block;
            vertical-align: top;
            box-sizing: border-box;
        
            .btn_dropdown_store {
                display: inline-block;
                position: relative;
            
                // 아이콘
                [class^=ico_] {
                    height: 24px;
                    margin-left: 6px;
                }
            
                .store_header_name {
                    display: inline-block;
                }
            
                .ico_arw {
                    width: 24px;
                    height: 24px;
                    box-sizing: border-box;
                    background: {
                        position: center;
                        repeat: no-repeat;
                    }
                    @include mixins.ratio_images('../images/btn_more_cont', 'png', 24px, 24px);
                    @include mixins.transition(background 0.2s ease-out);
                }
            
                .text {
                    @include mixins.font_size_md;
                    font-weight: variables.$fontWeightBold;
                }
            }
        
            &.active {
                .btn_dropdown_store {
                
                    .ico_arw {
                        @include mixins.ratio_images('../images/btn_more_cont_active_black', 'png', 24px, 24px);
                    }
                }
            
            
                .store_dropdown_inner {
                    opacity: 1;
                    @include mixins.transform(scaleY(1));
                
                }
            }
        
            .store_dropdown_inner {
                display: block;
                position: absolute;
                left: 0;
                top: 37px;
                z-index: 11;
                overflow: hidden;
                width: 196px;
                height: 286px;
                padding: 0 19px 19px;
                margin-top: 6px;
                background-color: #fff;
                border: 1px solid #000;
                border-radius: 10px;
                text-align: left;
                box-sizing: border-box;
                opacity: 0;
                @include mixins.transform(scaleY(0));
                @include mixins.transition(opacity 0.3s ease-out);
            
                .store_dropdown_contents {
                    box-sizing: border-box;
                
                    .store_area_dropdown_swiper_wrap {
                    
                        .store_area_title {
                            padding: 13px 28px 14px;
                            @include mixins.font_size_sm;
                            font-weight: variables.$fontWeightBold;
                            width: 100%;
                            text-align: center;
                            box-sizing: border-box;
                        }
                        .custom_scroll_wrap {
                            height: calc(235px - 19px);
                        }
                    
                        .store_point_list {
                        
                            .store_point_item {
                                +.store_point_item {
                                    margin-top: 10px;
                                }
                            }
                        }
                    
                        .swiper-button-prev,
                        .swiper-button-next {
                            position: absolute;
                            top: 13px;
                            left: 0;
                            display: inline-block;
                            width: 23px;
                            height: 23px;
                            margin-top: 0;
                            background: {
                                position: right center;
                                repeat: no-repeat;
                                color: transparent;
                            }
                            @include mixins.ratio_images('../images/ico_store_dropdown_arrow_h6w11', 'png', 6px, 11px);
                            vertical-align: top;
                        
                            &.swiper-button-disabled {
                                opacity: .3;
                            }
                        }
                        .swiper-button-next {
                            left: auto;
                            right: 0;
                        }
                    
                    }
                
                
                }
            }
        
        }//store_dropdown_wrap
        
        //매장안내 상단 소개 영역
        .store_detail_header {
        
            .header_contents_inner {
            
                //매장안내 메뉴 영역
                .store_header_menu_wrap {
                    position: relative;
                    width: 100%;
                    /*height: 70px;*/
                    background-color: #fff;
                
                    &:before {
                        content: '';
                        position: absolute;
                        top: 70px;
                        display: block;
                        width: 100%;
                        height: 1px;
                        background: variables.$colorGray04;
                    }
                
                    .contents_inner {
                        position: relative;
                        @include mixins.justify_width(start, end);
                    
                    
                        .store_header_wrap {
                            background: transparent;
                            height: 70px;
                            z-index: 2;
                        
                            .sps_inner {
                                @include mixins.justify_width(center);
                                height: 70px;
                            }
                        
                            .store_header_name {
                                @include mixins.justify_width(center);
                                width: auto;
                                font-size: 0;
                                line-height: 0;
                            
                                .text {
                                    @include mixins.font_size_md;
                                    font-weight: variables.$fontWeightBold;
                                }
                            
                                .ico_store_pin {
                                    display: inline-block;
                                    width: 16px;
                                    height: 30px;
                                    margin-left: 0;
                                    margin-right: 4px;
                                    background: {
                                        repeat: no-repeat;
                                        position: center;
                                    };
                                    @include mixins.ratio_images('../images/ico_store_pin', 'png', 16px, 30px);
                                    vertical-align: top;
                                }
                            }
                            
                            .store_dropdown_wrap {
                                margin-top: -999px;
                            }
                        
                        
                            &.sps-blw {
                                background: #fff;
                                z-index: 11;
    
                                .store_header_name {
                                    display: none;
                                }
    
                                .store_dropdown_wrap {
                                    margin-top: 0;
                                    .store_header_name {
                                        display: flex;
                                    }
                                }
                            }
                        }
                    
                    
                        .header_right_area {
                            position: relative;
                            justify-content: flex-end;
                            width: calc(100% - 500px);
                            z-index: 3;
                            //height: 70px;
                        
                            .before_login_my_box {
                                @include mixins.justify_width(center, center);
                                width: 100%;
                                height: 100%;
                            
                                .before_setting_desc {
                                    @include mixins.font_size_xs;
                                    color: variables.$textColorLightGray;
                                }
                            
                                .btn_pill {
                                    margin-left: 12px;
                                    border-radius: 19px;
                                
                                    .text {
                                        @include mixins.font_size_xxs;
                                    }
                                }
                            }
                        
                            .tab_wrap {
                                &.store_area {
                                    max-height: 235px;
                                    .tab_list_wrap {
                                        margin: 0;
                                        border-bottom: 1px solid variables.$colorGray04;
                                        height: 71px;
                                    
                                        .tabs {
                                            .tab_item {
                                                .tab_link {
                                                    padding: 0 23px;
                                                    .tab_text {
                                                        @include mixins.font_size_sm;
                                                        /*font-size: variables.$fontSizeSm;
                                                        letter-spacing: variables.$letterSpacing;*/
                                                        line-height: 17px;
                                                        color: variables.$textColorLightGray;
                                                        text-align: left;
                                                    }
                                                    &:before {
                                                        content: '';
                                                        display: block;
                                                        position: absolute;
                                                        top: 50%;
                                                        left: 0;
                                                        width: 1px;
                                                        height: 17px;
                                                        background: rgba(variables.$colorGray03,.5);
                                                        @include mixins.transform(translateY(-50%));
                                                    }
                                                    &:after {
                                                        height: 2px;
                                                    }
                                                }
                                                &:first-child {
                                                    .tab_link {
                                                        &:before {
                                                            top: 0;
                                                            bottom: 0;
                                                            height: auto;
                                                            background: rgba(variables.$colorGray04, .5);
                                                            @include mixins.transform(translateY(0));
                                                        }
                                                    }
                                                }
                                            
                                                &.ui-state-active, &.active {
                                                
                                                    .tab_link {
                                                        cursor: pointer;
                                                    
                                                        &:after {
                                                            background-color: #8f603d;
                                                        }
                                                    
                                                        .tab_text {
                                                            color: variables.$textColorBlack;
                                                            font-weight: variables.$fontWeightBold;
                                                        }
                                                    }
                                                }
                                            }
                                        
                                        }
                                    
                                    }//tab_list_wrap
                                
                                    .tab_content {
                                        height: 164px;
                                        padding: 20px 0;
                                        background-color: #fff;
                                        box-sizing: border-box;
                                        border-left: 1px solid rgba(variables.$colorGray04, .5);
                                    
                                        .store_point_list {
                                            height: 124px;
                                        
                                            .store_point_item {
                                                width: calc(100%/4);
                                                padding-left: 23px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            
                //매장안내 상세 상단
                .store_detail_title_wrap {
                    position: relative;
                    padding: 0 0 60px;
                    min-height: 437px;
                
                    .contents_inner {
                        @include mixins.justify_width;
                        .left_area {
                            width: 500px;
                        
                            .store_info_box {
                                padding-right: 50px;
                            
                                .store_name {
                                    font-size: 0;
                                    line-height: 0;
                                    @include mixins.justify_width(end);
                                    .text {
                                        font-size: 56px;
                                        line-height: 62px;
                                        font-weight: variables.$fontWeightBold;
                                        letter-spacing: variables.$letterSpacing;
                                        word-break: break-word;
                                    }
                                    .btn_favorite_circle {
                                        margin-bottom: 10px;
                                    }
                                }
                            
                                .store_desc {
                                    display: block;
                                    margin-top: 18px;
                                    font-size: variables.$fontSizeDef;
                                    letter-spacing: variables.$letterSpacing;
                                }
                            
                                .store_info_list {
                                    margin-top: 30px;
                                
                                    .store_info_item {
                                        @include mixins.justify_width;
                                        font-size: variables.$fontSizeXxs;
                                        line-height: 21px;
                                        letter-spacing: variables.$letterSpacing;
                                        color: variables.$textColorGray;
                                    
                                        .info_title {
                                            position: relative;
                                            padding-right: 8px;
                                            margin-right: 8px;
                                        
                                            &:before {
                                                content: '';
                                                position: absolute;
                                                top: 6px;
                                                right: 0;
                                                width: 1px;
                                                height: 9px;
                                                background-color: variables.$colorGray03;
                                            }
                                        }
                                    
                                        .info_desc {
                                        }
                                    
                                        + .store_info_item {
                                            margin-top: 6px;
                                        }
                                    }
                                
                                }
                            
                                .btn_wrap {
                                    margin-top: 30px;
                                    text-align: left;
                                
                                    .btn_brown {
                                        background-color: #8f603d;
                                    
                                        .ico_pin {
                                            display: inline-block;
                                            width: 16px;
                                            height: 20px;
                                            margin-top: 1px;
                                            margin-right: 5px;
                                            background: {
                                                position: center;
                                                repeat: no-repeat;
                                            }
                                            @include mixins.ratio_images('../images/ico_pin', 'png', 16px, 20px);
                                        }
                                    
                                        .text {
                                            color: #fff;
                                            font-size: variables.$fontSizeXs;
                                            font-weight: variables.$fontWeightMedium;
                                            line-height: variables.$fontSizeXl;
                                        }
                                    }
                                    .btn_lighten_gray {
                                        font-weight: variables.$fontWeightMedium;
                                    }
                                }
                            }
                        }
                    
                    }//contents_inner
                
                
                    .right_area {
                        position: absolute;
                        top:0;
                        left: 50%;
                        margin-left: -100px;
                        width: calc(50% + 100px);
                    
                        .img_box {
                            text-align: left;
                            background: variables.$colorGray06;
                            img {
                                max-height: 375px;
                            }
                        }
                        //스토어 롤링 공지사항
                        .store_notice_wrap {
                            position: relative;
                            background: variables.$colorGray06;
                            height: 62px;
                            padding: 24px 22px;
                            box-sizing: border-box;
                        
                            @include mixins.justify_width(center);
                        
                            .title_wrap {
                                color: variables.$kyoboGreen;
                                margin-right: 12px;
                                padding-bottom: 0;
                            }
                        
                            .store_notice_rolling_swiper_wrap {
                                position: relative;
                                display: block;
                                width: 614px;
                                height: 19px;
                                overflow: hidden;
                                box-sizing: border-box;
                            
                                .store_notice_list {
                                    height: 19px;
                                
                                    .swiper-wrapper {
                                        width: calc( 100% - 80px );
                                    
                                        .store_notice_item {
                                        
                                            a.btn_text_link {
                                                width: 100%;
                                                @include mixins.font_size_xs;
                                                font-weight: variables.$fontWeightRegular;
                                                .text {
                                                    text-decoration: none;
                                                }
                                            
                                            }
                                        }
                                    }
                                
                                    .swiper_control_box {
                                        position: absolute;
                                        background: transparent;
                                        top: 50%;
                                        right: 0;
                                        left: auto;
                                        @include mixins.transform(translateY(-50%));
                                    }
                                }
                            }
                        }
                    
                    
                    }//right_area
                
                }
            }
        }//store_detail_header
    
        //매장안내 하단 탭 add
        .tab_wrap {
            &.store_detail_body {
                .tab_list_wrap {
                    position: relative;;
                    margin-bottom: 0;
                
                    .tab_list_inner {
                        &.sps {
                            height: 72px;
                            border-bottom: 1px solid variables.$colorGray03;
                            box-sizing: border-box;
                            &.sps_scroll_fixed {
                                width: 100%;
                                background-color: #fff;
                                z-index: 10;
                            }
                            &.sps-blw {
                                top: 69px;
                            }
                        
                            .sps_inner {
                                @include mixins.justify_width(center);
                                width: 1200px;
                                margin: 0 auto 30px;
                                height: 100%;
                                background-color: #fff;
                            
                                .tab_item {
                                
                                    &.ui-state-active, &.active {
                                        z-index: 2;
                                        margin-bottom: 0;
                                        padding-bottom: 0;
                                    
                                        .tab_link {
                                            cursor: pointer;
                                        
                                            &:after {
                                                background-color: #000;
                                            }
                                        
                                            .tab_text {
                                                color: variables.$textColorBlack;
                                                font-weight: variables.$fontWeightBold;
                                            }
                                        }
                                    }
                                }
                            
                                .right_area {
                                    display: inline-block;
                                    flex: 0 auto;
                                    align-self: center;
                                    margin-left: 30px;
                                
                                    .form_search_box {
                                        .form_ip_search {
                                            .form_ip {
                                                width: 320px;
                                                padding: 0 52px 2px 18px;
                                                border-radius: 30px;
                                                border-color: variables.$colorGray04;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }//tab_wrap
    
        //매장안내 하단 탭컨텐츠영역
        .store_detail_body {
            .store_detail_contents {
            
                //탭컨텐츠 공통
                .tab_content {
                    &.sps {
                        padding: 110px 0;
                        &:last-child {
                            padding-bottom: 0;
                        }
                    }
                
                    &.bg_brown {
                        background-color: rgba(143, 96, 61, 0.06);
                    }
                }
            
                // 오시는 길
                .store_direction_wrap {
                    position: relative;
                
                    .scroll_pos_top {
                        //background: rgba(green, .5);
                        //z-index: 10;
                        visibility: hidden;
                        position: absolute;
                        left: auto;
                        right: 0;
                        bottom: calc(488px + 175px);//지도고정타겟의 높이값 + top
                        width: 100px;
                        top: -175px;
                    }
                    .scroll_pos_bottom {
                        //background: rgba(yellow, .5);
                        //z-index: 10;
                        visibility: hidden;
                        position: absolute;
                        left: auto;
                        right: 0;
                        bottom: 0;
                        width :100px;
                        height: calc(488px + 175px);//지도고정타겟의 높이값 + top
                    }
                    .scroll_fixed_target {
                        position: absolute;
                        top: 0;
                        height: 488px;
                        z-index: 1;
                    
                        &.scroll_fixed {
                            position: fixed;
                            top: 175px;
                            bottom: auto;
                        }
                        &.bottom {
                            top: auto;
                            bottom: 0;
                        }
                    }
                
                    .left_area {
                        width: 630px;
                    
                        .store_direction_map {
                            position: absolute;
                            top: 0;
                            .title_wrap {
                                //매장 주소나오는 부분 2줄까지 나오도록 설정함
                                @include mixins.justify_width();
                                height: 60px;
                                padding-bottom: 13px;
                            }
                        
                            .store_map_contents {
                                display: inline-block;
                                width: 640px;
                                vertical-align: top;
                            }
                        }
                    }
                
                    .right_area {
                        position: relative;
                        width: calc(100% - 689px);
                        padding: 0 60px 0 50px;
                        margin-left: 689px;
                        border-left: 1px solid variables.$colorGray04;
                        box-sizing: border-box;
                    
                        .tab_wrap {
                            min-height: 488px;
                            .tab_list_wrap {
                                margin-bottom: 32px;
                            }
                        }
                    
                    
                    
                        .public_traffic_guide_list {
                            .traffic_guide_item {
                                .guide_title {
                                    @include mixins.justify_width(center);
                                    font-size: 18px;
                                    line-height: 30px;
                                    font-weight: variables.$fontWeightBold;
                                
                                    [class^='ico_traffic'] {
                                        content: '';
                                        display: inline-block;
                                        width: 26px;
                                        height: 26px;
                                        margin-right: 4px;
                                        background: {
                                            repeat: no-repeat;
                                            position: center;
                                        }
                                    
                                        &.ico_traffic_subway {
                                            @include mixins.ratio_images('../images/ico_traffic_subway', 'png', 26px, 26px);
                                        }
                                        &.ico_traffic_bus {
                                            @include mixins.ratio_images('../images/ico_traffic_bus', 'png', 26px, 26px);
                                        }
                                    }
                                }
                            
                                .guide_text {
                                    margin-top: 17px;
                                    @include mixins.font_size_def;
                                    color: variables.$textColorBlack;
                                
                                    + .bul_list {
                                        margin-top: 10px;
                                    
                                        .bul_item_dot {
                                            + .bul_item_dot {
                                                margin-top: 6px;
                                            }
                                        }
                                    }
                                }
                            
                                & ~ .traffic_guide_item {
                                    margin-top: 24px;
                                    padding-top: 24px;
                                    border-top: 1px solid variables.$colorGray04;
                                }
                            
                                .public_traffic_detail_list {
                                    margin-top: 17px;
                                
                                    .detail_item {
                                        .detail_title {
                                            font-size: variables.$fontSizeDef;
                                            font-weight: variables.$fontWeightMedium;
                                            line-height: variables.$fontSizeSmLineHeight;
                                        }
                                    
                                        & ~ .detail_item {
                                            padding-top: 16px;
                                            margin-top: 16px;
                                            border-top: 1px dashed variables.$colorGray04;
                                        
                                        }
                                    
                                        .detail_text {
                                            margin-top: 10px;
                                            @include mixins.font_size_xs;
                                            color: variables.$textColorGray;
                                        }
                                    }
                                }
                            
                            }
                        }
                    }
                }//store_direction_wrap
            
                //매장안내도
                .store_floor_infomation_wrap {
                
                    .tab_wrap {
                        position: relative;
                    
                        .tab_list_wrap {
                            position: absolute;
                            top: 0;
                            right: 0;
                            width: 660px;
                            background: transparent;
                        
                            .tabs {
                                .tab_item {
                                    &.ui-state-active{
                                        background-color: #8f603d;
                                        border-color: #8f603d;
                                    }
                                }
                            }
                        }
                    
                        .tab_content {
                            background-color: transparent;
                        
                            .store_floor_map_wrap {
                                .right_area {
                                    margin-top: 74px;
                                }
                            }
                        }//tab_content
                    }//tab_wrap
                
                }//store_floor_infomation_wrap
            
                //이벤트 & 공지사항
                .title_wrap {
                    .sns_insta_box {
                        margin-left: 20px;
                    
                        .btn_sns_insta {
                            @include mixins.justify_width(center);
                            height: 34px;
                            padding-right: 15px;
                            vertical-align: top;
                            border: 1px solid rgba(#902cab, .23);
                            border-radius: 30px;
                        
                            .ico_insta {
                                display: inline-block;
                                width: 32px;
                                height: 32px;
                                margin-right: 7px;
                                @include mixins.ratio_images('../images/btn_footer_sns_insta', 'png', 32px, 32px);
                            }
                        
                            .text {
                                font-size: variables.$fontSizeXxs;
                                color: variables.$textColorGray;
                                letter-spacing: variables.$letterSpacing;
                                line-height: variables.$fontSizeSmLineHeight;
                            }
                        }
                    }
                }
    
                //sns
                .sns_post_list {
                    @include mixins.justify_width;
                    align-items: stretch;
                    flex-wrap: wrap;
        
                    .sns_post_item {
                        display: inline-block;
                        flex-shrink: 0;
                        width: calc((100% - 36px * 4) / 5);
                        margin-left: 36px;
                        background-color: #fff;
                        border-radius: 16px;
                        vertical-align: top;
                        box-sizing: border-box;
            
                        &:nth-child(5n+1) {
                            margin-left: 0;
                        }
            
                        &:nth-child(n+6) {
                            margin-top: 35px;
                        }
            
                        .post_area {
                
                            .post_link {
                                display: block;
                                width: 100%;
                    
                                .post_thumb_box {
                                    position: relative;
                                    display: block;
                                    border-radius: 16px;
                                    overflow: hidden;
                        
                                    .img_box {
                                        display: block;
                            
                                        img {
                                            width: 100%;
                                        }
                                    }
                        
                                    .post_info_box {
                                        display: none;
                                        position: absolute;
                                        top: 0;
                                        left: 0;
                                        width: 100%;
                                        height: 100%;
                                        padding: 30px;
                                        background-color: rgba(0,0,0, 0.7);
                                        box-sizing: border-box;
                                        text-align: center;
                            
                                        .post_text {
                                            @include mixins.font_size_xs;
                                            color: #fff;
                                        }
                                    }
                                }
    
                                &:hover {
									.post_info_box {
                                        @include mixins.justify_width(center);
                                    }
                                    
                                }
    
                            }
                        }
                    }
                    
                    +.sns_post_list{
                        margin-top: 35px;
                    }
                }//sns_post_list
            
                // 시설/서비스
                .store_facilities_swiper_wrap {
                    .swiper-container {
                        border-radius: 16px;
                        overflow: hidden;
                        .swiper-wrapper {
                            .swiper-slide {
                                overflow: hidden;
                            
                                img {
                                    width: 100%;
                                }
                            
                                &:first-child {
                                    border-radius: 16px 0 0 16px;
                                }
                                &:last-child {
                                    border-radius: 0 16px 16px 0;
                                }
                            }
                        }
                    }
                    .swiper-button-prev {
                        left: -26px;
                    }
                    .swiper-button-next {
                        right: -26px;
                    }
                
                    .facilities_desc {
                        margin-top: 20px;
                        font-size: variables.$fontSizeMd;
                        line-height: variables.$fontSizeXsLineHeight;
                        font-weight: variables.$fontWeightBold;
                        letter-spacing: variables.$letterSpacing;
                    }
                    .facilities_sdesc {
                        margin-top: 10px;
                        font-size: variables.$fontSizeSm;
                        line-height: variables.$fontSizeXsLineHeight;
                        letter-spacing: variables.$letterSpacing;
                    }
                }
            
            }//store_detail_contents
        }//store_detail_body
        
        //층별맵
        .store_floor_map_wrap {
        
            @include mixins.justify_width;
            background-color: transparent;
        
            .left_area {
                width: 490px;
                align-self: stretch;
                border-right: 1px solid variables.$colorGray04;
            
                .store_floor_info_list {
                    border-top: 1px solid variables.$textColorBlack;
                    border-bottom: 1px solid variables.$colorGray04;
                
                    .store_floor_info_item {
                        @include mixins.clearfix;
                        padding: 25px 0;
                    
                        .store_location {
                            display: inline-block;
                            float: left;
                            width: 45px;
                            margin-right: 5px;
                            @include mixins.font_size_xs;
                            color: variables.$textColorBlack;
                            font-weight: variables.$fontWeightBold;
                            vertical-align: top;
                        }
                    
                        .store_field {
                            display: inline-block;
                            float: left;
                            width: calc(100% - 65px);
                            padding-right: 15px;
                            @include mixins.font_size_sm;
                            vertical-align: top;
                        }
                    
                        & + .store_floor_info_item {
                            border-top: 1px solid variables.$colorGray04;
                        }
                    }
                }
            }//left_area
        
            .right_area {
                margin-top: 74px;
                width: 659px;
                padding-left: 50px;
            
            
                .store_map_img_wrap {
                    position: relative;
                    width: 659px;
                    height: 858px;
                    border:solid 1px variables.$colorGray04;
                    box-sizing: border-box;
                
                    .store_map_controls {
                        position: absolute;
                        top: 24px;
                        right: 24px;
                        display: inline-block;
                        width: 89px;
                        font-size: 0;
                        vertical-align: top;
                    
                        [class^='btn_zoom_'] {
                            display: inline-block;;
                            width: 50%;
                            height: 44px;
                            border: 1px solid variables.$colorGray02;
                            background-color: #fff;
                            vertical-align: top;
                            box-sizing: border-box;
                        
                            & + [class^='btn_zoom_'] {
                                border-left: 0;
                            }
                        
                            &:after {
                                content: '';
                                display: block;
                                width: 12px;
                                height: 12px;
                                margin: 0 auto;
                            }
                        
                            &.btn_zoom_in {
                                border-radius: 0 6px 6px 0;
                            
                                &:after {
                                    position: relative;
                                    @include mixins.ratio_images('../images/ico_plus_h12', 'png', 12px, 12px);
                                }
                            }
                        
                            &.btn_zoom_out {
                                border-radius: 6px 0 0 6px;
                            
                                &:after {
                                    position: relative;
                                    @include mixins.ratio_images('../images/ico_minus_h12', 'png', 12px, 12px);
                                }
                            }
                        
                        }
                    }//store_map_controls
                }//store_map_img_wrap
            }//right_area
        }//store_floor_map_wrap
    
        //지역별 매장리스트
        .store_point_list {
            overflow: hidden;
            display: inline-flex;
            flex-wrap: wrap;
            flex-direction: row;
            align-content: baseline;
            width: 100%;
        
            .store_point_item {
                width: 100%;
                font-size: variables.$fontSizeXxs;
                box-sizing: border-box;
            
                .store_point_link {
                    &.active {
                        text-decoration: underline;
                        font-weight: variables.$fontWeightBold;
                    }
                }
            
                + .store_point_item {
                    margin-top: 5px;
                }
            }
        }//store_point_list
    
        // 서비스 리스트
        .service_list {
            @include mixins.justify_width;
            align-items: stretch;
            flex-wrap: wrap;
        
            .service_item {
                display: inline-block;
                flex-shrink: 0;
                width: calc((100% - 24px * 2) / 3);
                padding: 29px 29px;
                margin-left: 24px;
                background-color: #fff;
                border: 1px solid variables.$colorGray04;
                border-radius: 16px;
                vertical-align: top;
                box-sizing: border-box;
            
                &:nth-child(3n+1) {
                    margin-left: 0;
                }
            
                &:nth-child(n+4) {
                    margin-top: 24px;
                }
            
                .service_title {
                    @include mixins.justify_width(center);
                    margin-bottom: 20px;
                
                    .img_box {
                        display: inline-block;
                        flex: 0 auto;
                        margin-right: 12px;
                        vertical-align: top;
                    }
                
                
                    .title {
                        display: inline-block;
                        flex: 1;
                        color: variables.$textColorBlack;
                        @include mixins.font_size_def;
                        font-weight: variables.$fontWeightBold;
                        vertical-align: top;
                    }
                }
            
                .service_cont {
                    .info_text {
                        & + .bul_list {
                            margin-top: 12px;
                        
                            .bul_item_dot {
                            
                                + .bul_item_dot {
                                    margin-top: 6px;
                                }
                            }
                        }
                    }
                
                    .btn_more_view {
                        margin-top: 12px;
                    }
                }
            }
        }//service_list
    
    
        // 매장안내 > 이벤트&공지사항 목록
        .store_board_list_wrap {
            margin-top: 50px;
        }//store_board_list_wrap
    
        // 매장안내 > 이벤트&공지사항 상세
        .store_board_view_wrap {
            margin-top: 50px;
            .view_detail_wrap {
                .view_detail_header {
                    .edit_info_case {
                        margin-top: 11px;
                        .editor_name {
                            display: inline-block;
                            @include mixins.font_size_xs;
                            color: variables.$textColorLightGray;
                            vertical-align: top;
                        
                            + .date {
                                position: relative;
                                margin-left: 8px;
                                padding-left: 8px;
                            
                                &:before {
                                    content: '';
                                    display: block;
                                    position: absolute;
                                    top: 50%;
                                    left: 0;
                                    transform: translateY(-50%);
                                    width: 1px;
                                    height: 9px;
                                    background-color: variables.$colorGray03;
                                }
                            }
                        }
                    }
                }
            
                .view_detail_body {
                
                    &.has_line {
                        padding-top: 24px;
                        border-bottom-color: variables.$colorGray03;
                    }
                
                    .edit_write_case {
                        & + .attached_img_box {
                            margin-top: 36px;
                        }
                    }
                
                    .attached_img_box {
                        img {
                            width: 100%;
                            max-width: 100%;
                        }
                    }
                }
                .view_detail_footer {
                    border-bottom: 1px solid variables.$colorGray04;
                
                    .board_item {
                        @include mixins.justify_width(center);
                        overflow: hidden;
                        height: 76px;
                        padding: 0 20px;
                    
                        .prev_board, .next_board {
                            flex-shrink: 0;
                            display: inline-block;
                            margin-right: 36px;
                            @include mixins.font_size_xs;
                            vertical-align: top;
                        
                            [class^=ico_board] {
                                display: inline-block;
                                width: 8px;
                                height: 5px;
                                margin: 8px 0 0 6px;
                                background: {
                                    position: center;
                                    repeat: no-repeat;
                                }
                                vertical-align: top;
                            
                                @include mixins.ratio_images('../images/ic_event_noti_detail_arr', 'png', 8px, 5px);
                            
                            
                                &.ico_board_next {
                                    transform: rotate(180deg);
                                }
                            }
                        }
                    
                        .board_title {
                            flex: 1 1 auto;
                            @include mixins.ellipsis();
                            @include mixins.font_size_xs;
                        }
                    
                        .desc {
                            flex: 1 1 auto;
                            @include mixins.font_size_xs;
                            color: variables.$textColorLightGray;
                        }
                    
                        + .board_item {
                            border-top: 1px solid variables.$colorGray04;
                        }
                    }
                }
            }
        }//store_board_view_wrap
    }
}

